import i18next from "i18next";
import { FC } from "react";
import { Container, Button, ButtonGroup } from "react-bootstrap";
import { initReactI18next, useTranslation } from "react-i18next";

const transRes = {
  en: {
    translation: {
      text: `O that you were your self! but, love, you are
      No longer yours than you yourself here live;
      Against this coming end you should prepare,
      And your sweet semblance to some other give:
      So should that beauty which you hold in lease
      Find no determination; then you were
      Your self again after yourself's decease,
      When your sweet issue your sweet form should bear.
      Who lets so fair a house fall to decay,
      Which husbandry in honour might uphold
      Against the stormy gusts of winter's day
      And barren rage of death's eternal cold?
      O, none but unthrifts: dear my love, you know
      You had a father, let your son say so.`,
    },
  },
  fr: {
    translation: {
      text: `Oh ! si vous existiez par vous-mme ! mais, ami, vous ne vous appartiendrez plus ds que vous aurez vcu votre vie ici-bas. Prparez-vous contre cette fin fatale, et donnez votre douce ressemblance  quelque autre.
      Par l, cette beaut, que vous avez  bail, n'aura pas de terme : ainsi vous vous survivrez, aprs votre dcs mme, dans cette douce famille qui gardera votre forme douce.
      Qui donc laisserait tomber en ruine une maison si belle, quand les soins du mnage pourraient la conserver en honneur contre les rafales des jours d'hiver et la rage funeste de cette bise ternelle, la mort ?
      Oh ! nul autre qu'un prodigue ! Cher amour, vous savez, vous avez eu un pre : puisse votre fils en dire autant !`,
    },
  },
  de: {
    translation: {
      text: `Oh wrst du doch du selbst! Doch, Liebling, du
      bist's nicht, wenn du allein zu sein gedenkst,
      sorg vor frs nahe Ende und sieh zu,
      dass du dein Ausseh'n jemand and'rem schenkst.
      Die Schnheit, die dir nur auf Zeit gegeben,
      wird so nicht untergeh'n, wirst du einst sterben,
      denn auch du selbst wirst dann noch berleben,
      wenn deine Kinder deine Schnheit erben.
      Wer will schon, dass ein schnes Haus verfllt,
      wenn es ein Ehepaar erhalten kann
      bei allen Widrigkeiten dieser Welt
      und auch des Todes eisig kaltem Bann?
      Du konntest, Schatz, jemanden Vater nennen,
      das soll ein Sohn zu dir auch sagen knnen.`,
    },
  },
  es: {
    translation: {
      text: `Oh! Si vos, fuerais vuestro! Pero, amor, voz seris,
      de voz tan solamente, mientras vivis aqu.
      Contra el final cercano ya debis prepararos,
      plasmando en algn otro, vuestro dulce semblante.
      As, aquella belleza, que vos gozis a prstamo
      no hallara final. Entonces volverais,
      a ser vos, an despus, de vuestra propia muerte,
      ya que la dulce prole, tendr tus mismas formas.
      Quin deja derrumbarse, un hogar tan hermoso,
      que un regente viril, con honor mantendra,
      contra los elementos de un cruel da de invierno,
      y el estril rencor del fro de la muerte?
      Slo un derrochador y t, amor, bien lo sabes:
      Vos tuvisteis un padre, que a ti, te nombre un hijo.`,
    },
  },
};

i18next.use(initReactI18next).init({ resources: transRes, lng: "en" });

const ChangeLanguage: FC = () => {
  const { t, i18n } = useTranslation();
  return (
    <Container className="mt-3">
      <ButtonGroup>
        <Button
          onClick={() => {
            i18n.changeLanguage("en");
          }}
        >
          EN
        </Button>
        <Button
          onClick={() => {
            i18n.changeLanguage("fr");
          }}
        >
          FR
        </Button>
        <Button
          onClick={() => {
            i18n.changeLanguage("de");
          }}
        >
          DE
        </Button>
        <Button
          onClick={() => {
            i18n.changeLanguage("es");
          }}
        >
          ES
        </Button>
      </ButtonGroup>
      <div className="mt-3">{t("text")}</div>
    </Container>
  );
};

export default ChangeLanguage;
